import Footer from "./Footer";
import Note from "./Note";
import Header from "./Header";
import CreateArea from "./CreateArea";
import { useEffect, useState } from "react";
import React from 'react'


function App()
{
    const initialNotes = localStorage.getItem("notes") ? JSON.parse(localStorage.getItem("notes")) : [];
    const [notes,setNotes] = useState(initialNotes);

    function addNote(event)
    {
        const inp = event.target.title.value;
        const txt = event.target.content.value;

        setNotes(prev => [...prev,{title: inp, content: txt}]);
        event.preventDefault();
    }

    function handleDelete(id)
    {
        setNotes((prev) => {
            return prev.filter((cur,i) => {
                return i !== id;
            })
        });
    }

    useEffect(()=>{
        localStorage.setItem("notes", JSON.stringify(notes));
    },[notes])

    function handleEdit(event,id,s)
    {
        const val = event.target.value;

        if(s === "t")
        {
            setNotes((prev)=> [...prev.slice(0,id), {...prev[id], title: val}, ...prev.slice(id+1)])
        }
        else if(s === "c")
        {
            setNotes((prev)=> [...prev.slice(0,id), {...prev[id], content: val}, ...prev.slice(id+1)])
        }
        
    }

    return(
        <div className="container">
            <div className="content">
                <Header />
                <CreateArea add={addNote} />
                <ul className="notes">
                    {notes.map((x,i) => <Note key={i} id={i} change={handleEdit} title={x.title} content={x.content} onDelete={handleDelete} />)}
                </ul>
            </div>
            <Footer />
        </div>
    )
}

export default App;