import React, { useState } from "react";
import DeleteIcon from '@mui/icons-material/Delete';
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';

function Note(props)
{
    const [titleOver,setTitleOver] = useState(false);
    const [paraOver,setParaOver] = useState(false);
    const [titleEdit, setTitleEdit] = useState(false);
    const [contentEdit, setContentEdit] = useState(false);

    function titleOn()
    {
        setTitleOver(true);
    }

    function titleOff()
    {
        setTitleOver(false);
    }

    function paraOn()
    {
        setParaOver(true);
    }

    function paraOff()
    {
        setParaOver(false);
    }

    function handleKey(e,s)
    {
        if(e.key === "Enter")
        {
            if(s === "t") setTitleEdit(false);
            else if(s === "c") setContentEdit(false);
        }
    }



    return(
        <div className="note">
            {titleEdit ? <input type="text" autoFocus value={props.title} onChange={(e)=>props.change(e,props.id,"t")} onKeyDown={(event)=>handleKey(event,"t")} /> : <h1 style={titleOver ? {backgroundColor:  "#f4f1f1"} : null} onMouseOver={titleOn} onMouseOut={titleOff}>{props.title} &nbsp; {titleOver && <EditTwoToneIcon onClick={()=>setTitleEdit(true)} fontSize="inherit" />}</h1>}

            {contentEdit ? <input type="text" autoFocus value={props.content} onChange={(e)=>props.change(e,props.id,"c")} onKeyDown={(event)=>handleKey(event,"c")}/> : <p style={paraOver ? {backgroundColor:  "#f4f1f1"} : null} onMouseOver={paraOn} onMouseOut={paraOff}>{props.content} &nbsp; {paraOver && <EditTwoToneIcon onClick={()=>setContentEdit(true)} fontSize="inherit" />}</p>}

            <button onClick={()=>props.onDelete(props.id)} className="delete">
                <DeleteIcon />
            </button>
        </div>
    ) ;
}


export default Note;