import React, { useState } from "react";
import AddIcon from '@mui/icons-material/Add';
import Fab from '@mui/material/Fab';
import Zoom from '@mui/material/Zoom';


function CreateArea(props)
{
    const [isExpanded, setExpand] = useState(false);

    const [note, setNote] = useState({
        title: "",
        content: ""
    });

    function handleChange(event)
    {
        const {name, value} = event.target;

        setNote((prev) => {return {...prev,[name]: value}})
    }

    function expand()
    {
        setExpand(true);
    }

    return (<div>
        <form className="create-note" onSubmit={(event)=> {
            props.add(event);
            return setNote({title: "", content: ""});;
        }}>
            {isExpanded && <input onChange={handleChange} name="title" placeholder="Title" value={note.title}></input>}
            <textarea onClick={expand} onChange={handleChange} name="content" placeholder="Take a note..." value={note.content} rows={isExpanded ? 3 : 1}></textarea>
            <Zoom in={isExpanded}><Fab type="submit"><AddIcon /></Fab></Zoom>
        </form>
    </div>);
}

export default CreateArea;